import * as firebase from "firebase/app";
import { useState, useEffect } from "react";

export default function useLoginStatus() {
  const [isLoggedIn, setIsLoggedIn] = useState(null);

  useEffect(() => {
    firebase.auth().onAuthStateChanged(user => {
      if (user != null) {
        setIsLoggedIn(true);
      } else {
        setIsLoggedIn(false);
      }
    });
  });

  return isLoggedIn;
}
